import Vivus from 'vivus';

class vivusHelper {
  static setVivusIcon(divId, linkToIcon) {
    return new Vivus(divId, {
      duration: 2000,
      file: linkToIcon,
      type: 'oneByOne',
      onReady(myVivus) {
        myVivus.el.setAttribute('height', '100px');
      }
    });
  }
}

export default vivusHelper;



